.App {
  text-align: center;
  padding: 1rem 3rem 3rem;
  background:#fff;
}

.row {
  display: flex;
  justify-content: space-around;
  flex-wrap: wrap;
  align-items: center;
}

label {
  padding: 1rem 0;
  display: block;
}

.select-css, button {
  background-color: #b9f78a;
  display: block;
	font-size: 16px;
	font-family: sans-serif;
	font-weight: 700;
	color: #444;
	line-height: 1.3;
	padding: .6em 1.4em .5em .8em;
	width: 100%;
	max-width: 100%;
	box-sizing: border-box;
	margin: 0;
	border: 1px solid transparent;
	box-shadow: 0 1px 0 1px rgba(0,0,0,.04);
	border-radius: .5em;
	-moz-appearance: none;
	-webkit-appearance: none;
	appearance: none;
	background-image: url('data:image/svg+xml;charset=US-ASCII,%3Csvg%20xmlns%3D%22http%3A%2F%2Fwww.w3.org%2F2000%2Fsvg%22%20width%3D%22292.4%22%20height%3D%22292.4%22%3E%3Cpath%20fill%3D%22%23007CB2%22%20d%3D%22M287%2069.4a17.6%2017.6%200%200%200-13-5.4H18.4c-5%200-9.3%201.8-12.9%205.4A17.6%2017.6%200%200%200%200%2082.2c0%205%201.8%209.3%205.4%2012.9l128%20127.9c3.6%203.6%207.8%205.4%2012.8%205.4s9.2-1.8%2012.8-5.4L287%2095c3.5-3.5%205.4-7.8%205.4-12.8%200-5-1.9-9.2-5.5-12.8z%22%2F%3E%3C%2Fsvg%3E');
	background-repeat: no-repeat, repeat;
	background-position: right .7em top 50%, 0 0;
	background-size: .65em auto, 100%;
}

button {
  background-image: none;
  margin: 1rem 0;
  max-width: 1175px;
  font-size: 2rem;
}

.unpausedButton {
  background-color: #feff81;
}

.muscleRow {
  max-width: 1175px;
  flex-wrap: wrap;
  margin: 0 auto;
}

.muscleRowTitle {
  margin-top: 1.5rem;
}

.select-css::-ms-expand {
	display: none;
}
.select-css:hover {
	border-color: #888;
}
.select-css:focus {
	border-color: #aaa;
	box-shadow: 0 0 1px 3px rgba(59, 153, 252, .7);
	box-shadow: 0 0 0 3px -moz-mac-focusring;
	color: #222;
	outline: none;
}
.select-css option {
	font-weight:normal;
}

.checkboxWrapper {
  position: relative;
  display: flex;
  margin: 1rem 0;
}

.checkboxLabel, .checkboxWrapper input {
  opacity: 0;
}

.checkboxLabel {
  padding: 2rem;
}

.fakeCheckbox {
  position: absolute;
  pointer-events: none;
  top: 0;
  padding: 2rem;
  border-radius: 0.5em;
  border: 1px solid gray;
  color: gray;
}
.checkboxWrapper input:checked ~ .fakeCheckbox {
  background: #b9f78a;
  border: none;
  color: black;
}

.countdownTitle {
  margin-top: 0;
  font-size: 3rem;
  padding: 1rem;
  border-radius: 0.5em;
}
.restText {
  background-color: #feff81;
}
.workText {
  background-color: #b9f78a;
}

.exerciseTitle {
  font-size: 3rem;
}
.pageTitle {
  margin: 0.5rem auto;
}
.countdown {
  font-size: 10rem;
  font-weight: bold;
  margin: 0;
}

.mainRow {
  min-height: 70vh;
  border-radius: 2rem;
}
.mainRow .column {
  width: 65%;
}
.mainRow .column.bigger {
  max-width: 600px;
}
.mainRow .column.smaller {
  width: 35%;
}
.mainRow img {
  width: 100%;
}
.settingsRow {
  min-height: 50vh;
}

.previewButton {
  font-weight: normal;
  background: #feff81;
}

.progressBar {
  height: 1rem;
  background: lightgray;
  border: 1px solid lightgray;
  border-radius: 15px;
  position: relative;
  width: 80%;
  margin-bottom: -0.5rem;
}

.fill {
  width: 0%;
  background: #b9f78a;
  position: absolute;
  top: 0;
  left: 0;
  height: 100%;
  border-radius: 15px 0 0 15px;
  transition: width 0.25s;
  z-index: 1;
}

.progressRow {
  width: 100%;
}

.centerize {
  align-items: center;
  border-radius: 1rem;
  padding: 2rem 0;
}

.faq {
  max-width: 800px;
  margin: 0 auto;
  line-height: 2.2rem;
}

.previewRow p {
  font-size: 1.5rem;
}

.fullscreenButton, .fullscreenButton button {
  font-size: 0.5rem;
  width: 4rem;
  position: absolute;
  top: 0;
  right: 0;
}

.previewImageWrapper {
  position: relative;
  overflow: hidden;
  margin: 0 auto;
}

.previewImageWrapper img {
  width: 100%;
}

.bodyweightBanner {
  background: #feff81;
  position: absolute;
  top: 2rem;
  right: -2.5rem;
  width: 11rem;
  transform: rotate(45deg);
  height: 2rem;
  line-height: 2rem;
}

@media only screen and (max-width: 888px) {
  .exerciseTitle, .countdownTitle, button {
    font-size: 1.75rem;
  }
  .countdown {
    font-size: 4rem;
  }
  .countdownTitle {
    padding: 0;
  }
  .checkboxLabel, .fakeCheckbox {
    padding: 1rem;
  }
  .previewImageWrapper {
    max-width: 90%;
  }
  .App {
    padding: 1rem;
  }
}